export const tools = {
	filters: {

	},
	computed: {

	},
	methods: {
		/**
		 * VIP
		 */
		checkVipExpiry() {
			// 
			let currentTime = new Date().getTime();

			//  VIP 
			let time = this.vuex_vipinfo?.expiredate || ''
			
			if(!time) {
				return ''
			}
			
			let expiryTime = new Date(time.replace(' ', 'T')).getTime(); //  ISO 8601 
			
			// 
			if (!expiryTime) {
				console.log('VIP');
				return false;
			}

			//  VIP 
			if (currentTime > expiryTime) {
				console.log('VIP ');
				return false
			} else {
				console.log('VIP ');
				return time; //  true
			}
		},
		//
		navigate(e) {
			if (e.href && e.href.indexOf('http') == -1) { //					

				this.$util.uniCopy({
					content: this.vuex_config.upload.cdnurl + e.href,
					success: () => {
						this.$u.toast(',')
					}
				})




			}
		},
		//
		diylinkpress(e) {
			e.ignore();
			this.goPage(e.href);
			return false;
		},
		//
		lookImage(index) {
			uni.previewImage({
				current: index,
				urls: this.imagesList,
				longPressActions: {
					itemList: ['', '', ''],
					success: function(data) {
						console.log(data)
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			});
		},
		//url
		copyUrl(url = '') {
			this.$util.uniCopy({
				content: url || window.location.href,
				success: () => {
					this.$u.toast('');
				},
				error: () => {
					console.log('')
				}
			})
		},
		//url
		copyText(text = '') {
			this.$util.uniCopy({
				content: text,
				success: () => {
					this.$u.toast('');
				},
				error: () => {
					console.log('')
				}
			})
		},
		//cdnurl
		cdnurl(url) {
			if (!/^((?:[a-z]+:)?\/\/|data:image\/)(.*)/.test(url)) {
				return this.vuex_config.upload.cdnurl + url;
			}
			return url;
		},
		// staticurl
		staticurl(url) {
			if (!/^((?:[a-z]+:)?\/\/|data:image\/)(.*)/.test(url)) {
				return (this.vuex_config?.upload?.cdnurl || 'https://yanliangliang.com') + '/static/images/' + url;
			}
			return url;
		},
		// stlticurl
		audiourl(url) {
			if (!/^((?:[a-z]+:)?\/\/|data:image\/)(.*)/.test(url)) {
				return (this.vuex_config?.upload?.cdnurl || 'https://yanliangliang.com') + '/static/mp3/' + url;
			}
			return url;
		},
		// stlticurl
		videourl(url) {
			if (!/^((?:[a-z]+:)?\/\/|data:image\/)(.*)/.test(url)) {
				return (this.vuex_config?.upload?.cdnurl || 'https://yanliangliang.com') + '/static/video/' + url;
			}
			return url;
		},
		//
		goPage(path, auth) {
			if (path == 'out') {
				this.$u.vuex('vuex_token', '');
				this.$u.vuex('vuex_user', {});
				this.$u.vuex('vuex_openid', '');
				this.$u.vuex('vuex_vipinfo', null);
				return;
			}
			if (auth && !this.vuex_token) {
				let pages = getCurrentPages();
				// route
				let lastPageUrl = pages[pages.length - 1].$page.fullPath;
				this.$u.vuex('vuex_lasturl', lastPageUrl);
				this.$u.route('/pages/login/mobilelogin');
				return;
			}
			uni.$u.route({
				url: path,
				complete(e) {
					console.log(e, path)
				}
			})
		},
		logistics(res) {

			if (this.vuex_config.logisticstype == 'kd100') {
				wx.navigateToMiniProgram({
					appId: 'wx6885acbedba59c14',
					path: `pages/result/result?nu=${res.expressno}&com=&querysource=third_xcx`, //nucomcom
					extraData: {
						foo: 'bar'
					},
					success: (res) => {
						// 
					}
				});
			} else {
				this.goPage(
					`/pages/order/logistics?nu=${res.expressno}&coname=${res.expressname}&order_sn=${res.order_sn}`
					);
			}





		}
	}
}
